.card{
    background-color: rgb(139, 247, 238);
    width: 230px;
    border-radius: 15px;
    box-shadow: 0px 0px 10px rgb(102, 102, 102);
    text-align: center;
    padding-bottom: 15px;
    font-weight: bold;
    margin: 20px;
    transition: 0.2s ease-in-out;
}

.card:hover{
    transform: scale(1.1);
}


.card img{
    display: block;
    width: 95%;
    margin: 0 auto;
}
.card .name{
    margin: 15px 0px;
}
.card .email{
    font-size: 14px;
    margin: 10px 0px 0px 0px;
}